.burger {
  @include vp-767 {
    cursor: pointer;
    position: relative;
    width: 24px;
    height: 12px;
    border-top: 2px solid $color-alice-blue;
    border-bottom: 2px solid $color-alice-blue;
    transition: 1s;
  }
}

.burger::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: $color-alice-blue;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger--active {
  @include vp-767 {
    position: absolute;
    width: 25px;
    height: 25px;
    top: 18px;
    right: 20px;
    border: none;
    align-self: start;
    background-color: transparent;
    border-radius: 25%;
  }
}

.burger--active::before,
.burger--active::after {
  content: "";
  position: absolute;
  width: 18px;
  height: 2px;
  background-color: $color-default-black;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger--active::before {
  transform: rotate(45deg);
}

.burger--active::after {
  transform: rotate(-45deg);
}
