    height: 25px;
    top: 18px;
    right: 20px;
    border: none;
    align-self: start;
    background-color: transparent;
    border-radius: 25%;
  }
}

.burger--active::before,
.burger--active::after {
  content: "";
  position: absolute;
  width: 18px;
  height: 2px;
  background-color: #000000;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger--active::before {
  transform: rotate(45deg);
}

.burger--active::after {
  transform: rotate(-45deg);
}

