.callback-form button:hover,
.callback-form button:focus {
  transform: scale(1.05);
  background-color: #e5e5e5;
}

.callback-form button:active {
  transform: scale(0.9);
}

.custom-checkbox {
  cursor: pointer;
  display: flex;
  gap: 5px;
  align-items: center;
  margin-top: -1px;
}
@media (max-width: 767px) {
  .custom-checkbox {
    gap: 8px;
    margin-top: -3px;
    padding-right: 50px;
  }
}
.custom-checkbox input:checked + .checkbox::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 6px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%) rotate(-45deg);
  border-bottom: 1px solid #101a56;
  border-left: 1px solid #101a56;
}

.checkbox {
  position: relative;
  display: inline-block;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .checkbox {
    min-width: 20px;
    height: 20px;
  }
}

.custom-checkbox span {
  font-size: 10px;
  line-height: 12px;
}

.custom-checkbox:hover {
  outline: solid #d8d8d9;
}

.custom-checkbox input:focus ~ .checkbox {
  transform: scale(1.1);
  outline: auto #101a56;
}

.map {
  display: grid;
  grid-template-columns: 48% auto;
  gap: 103px;
  padding: 120px 0;
}
@media (max-width: 1023px) {
  .map {
    gap: 28px;
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  .map {
    grid-template-columns: 1fr;
    gap: 36px;
    padding: 56px 0 65px;
  }
}

.map h3 {
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  margin: 0;
  margin-bottom: 33px;
}
@media (max-width: 1023px) {
  .map h3 {
    margin-bottom: 28px;
  }
}

.map address {
  display: flex;
  gap: 10px;
  margin-bottom: 22px;
  transition: 0.5s;
}
@media (max-width: 1023px) {
  .map address {
    padding-right: 36px;
  }
}
@media (max-width: 767px) {
  .map address {
    padding: 0;
  }
}
@media (max-width: 1023px) {
  .map address svg {
    margin-top: 6px;
  }
}

.map a {
  display: flex;
  gap: 10px;
  transition: 0.5s;
  width: fit-content;
  margin-right: auto;
}

.map a:hover,
.map a:focus {
  transform: scale(1.03);
}

.map span {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-style: normal;
  color: #011c40;
}

.map svg {
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .map svg {
    min-width: 19px;
  }
}

.map__contacts {
  padding: 74px 0 0 0;
}
@media (max-width: 1023px) {
  .map__contacts {
    padding-top: 64px;
  }
}
@media (max-width: 767px) {
  .map__contacts {
    padding-top: 0;
  }
}

.map__container {
  order: -1;
  width: 100%;
  height: 306px;
}
@media (max-width: 1023px) {
  .map__container {
    height: 335px;
  }
}
@media (max-width: 767px) {
  .map__container {
    order: 0;
    height: 316px;
  }
}

.map__container iframe {
  width: 100%;
  height: 100%;
}

.footer {
  background-color: #011c40;
  padding: 50px 0;
  min-width: 320px;
}
@media (max-width: 1023px) {
  .footer {
    padding: 44px 0;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 30px 0 27px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: auto 5fr 1fr;
  gap: 10px;
  width: 100%;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-columns: 1fr;
    gap: 22px;
  }
}

.footer__logo {
  margin: auto 0;
}
@media (max-width: 1023px) {
  .footer__logo {
    padding-top: 10px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin-right: auto;
  }
}

.footer__social-media {
  display: flex;
  flex-wrap: wrap;
  gap: 17px;
  align-self: center;
  margin-left: auto;
  margin-right: -4px;
  margin-bottom: -8px;
  padding: 0;
}
@media (max-width: 1023px) {
  .footer__social-media {
    gap: 8px;
    margin-right: -6px;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .footer__social-media {
    margin: -6px auto 0 -5px;
    padding: 0;
  }
}

.footer__social-media a {
  padding: 5px;
}

@media (max-width: 1023px) {
  .footer__social-media svg {
    transform: scale(0.8);
  }
}

.footer__social-media a:hover,
.footer__social-media a:focus {
  transition: 0.5s;
  transform: scale(1.2);
}

.mobile-menu {
  display: none;
  position: fixed;
  background-color: rgba(0, 43, 102, 0.5);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.mobile-menu__wrapper {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 30px auto;
  width: 284px;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 50;
  background-color: #f9fbfd;
}

.mobile-menu__logo {
  position: absolute;
  top: 20px;
  left: 15px;
}

.mobile-menu__nav {
  grid-column: 1/-1;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.mobile-menu__button {
  grid-column: 2/-1;
  grid-row: 1/2;
  justify-self: end;
  margin-top: 24px;
  margin-right: 17px;
}

.mobile-menu__button:hover,
.mobile-menu__button:focus {
  transform: scale(1.2) rotate(360deg);
  outline: none;
}

